<template>
<div id="record-product">
<van-nav-bar
  left-arrow
  class="record-product-nav"
   @click-left="leftClick"
>
<template #title>
	<font color="white">货品记录</font>
</template>
<template #right>
	<i @click="showPopup" style="display:block;margin-top:8px;"><img src="~assets/images/common/icon/add.svg"></i>
</template>
</van-nav-bar>
	<record-content/>
	<van-popup v-model="show"  :close-on-click-overlay=false  closeable close-icon="close" position="top" :style="{ minHeight: '30%' }" >
		<h3 class="popup-title">
			新增记录
		</h3>
		<div class="group-box">
			<van-cell-group inset>
				<van-field v-model="recordObj.name" label="名称" placeholder="请输入产品名" />
				<van-field v-model="recordObj.unit" label="单位" placeholder="单位(瓶/箱/个/只/...)" />
				<van-field v-model="recordObj.specs" label="规格" placeholder="一个单位包含多少数量" />
				<van-field v-model="recordObj.price" label="价格" placeholder="价格" />
			<van-field name="radio" label="状态">
				<template #input>
					<van-radio-group  v-model="recordObj.status	" checked direction="horizontal">
					<van-radio name="0">热销<span style="margin-right:20px;"></span></van-radio>
					<van-radio name="1">短缺</van-radio>
					</van-radio-group>
					</template>
				</van-field>
			</van-cell-group>
		</div>
		<div class="btn-box">
			<van-button type="primary" style="margin-right:15px;" @click="sureClick">确定</van-button>
			<van-button type="danger" @click="cancelClick">取消</van-button>
		</div>
	</van-popup>
	<div class="bottom-box">
		<div @click="carClick" class="carImg">
			<div v-if="listLen">
				<img src="~assets/images/common/icon/carfull.svg"/>
				<i class="badge">{{listLen}}</i>
			</div>
			<div v-else>
				<img src="~assets/images/common/icon/car.svg"/>
			</div>
		</div>
		<div @click="allCheckedClick" class="select-box">
			<i class="allSelect"   v-if="isAllChecked">
				全选
			</i>
			<i class="allSelect" v-else>
				取消
			</i>
		</div>
		<div @click="deleteClick" class="delete-box">
			<i class="allSelect"   v-show='isSelect'>
				删除
			</i>
		</div>
	</div>
</div>
	
</template>

<script>
import { NavBar,Field, CellGroup,Icon,Popup,Button,RadioGroup, Radio} from 'vant'
import RecordContent from './childComps/RecordContent'
import {addGood,deleteGoods} from 'network/good'


export default {
  name: 'RecordProduct',
	components:{
		[NavBar.name]:NavBar,
		[Field.name]:Field,
		[CellGroup.name]:CellGroup,
		[Icon.name]:Icon,
		[Popup.name]:Popup,
		[Button.name]:Button,
		[Radio.name]:Radio,
		[RadioGroup.name]:RadioGroup,
		RecordContent
	},
	data(){
		return{
			show:false,
			recordObj:{
				name:'',
				status:'0',
				unit:'',
				specs:'',
				price:0,
			},
			isAllChecked:true,
		}
	},
	methods:{	
		//返回上级
		leftClick(){
			this.$router.go(-1)
		},
		showPopup(){
			this.show=true
		},
		//取消
		cancelClick(){
			this.show=false
		},
		//确定-发送
		sureClick(){
			//做一下验证
			if(this.recordObj.name==''){
				this.$toast('产品名称不能为空')
				return false
			}else if(this.recordObj.unit==''){
				this.$toast('单位不能为空')
				return false
			}else if(this.recordObj.specs==''){
				this.$toast('规格不能为空')
				return false
			}else if(this.recordObj.price==''){
				this.$toast('价格不能为空')
				return false
			}else if(isNaN(this.recordObj.price)){
				this.$toast('价格必须是数字')
				return false
			}
			
			this.show=false
			let _self=this
			//发送到数据库
			addGood(this.recordObj).then(res=>{
				//解决方法 返回值res是一个生成的新对象
				_self.recordObj.id=res
				this.$store.state.recordObj.list.push(_self.recordObj)	//这里没有数据库需要的id
				_self.$toast("添加成功")
			})
		},
		carClick(){
			this.$router.push('/car').catch(err=>{err})
		},
		//全选按钮
		allCheckedClick(){
			let _self=this
			this.isAllChecked=!this.isAllChecked
			
			//先清空一下
			_self.$store.state.goodCarList=[]
			//这里需要在action里面操作
			this.recordList.forEach(item=>{
				
				item.checked=!_self.isAllChecked
				if(_self.isAllChecked==false){
					_self.$store.state.goodCarList.push(item)
				}else{
					_self.$store.state.goodCarList=[]
				}
				
			})
			//需要push选中的
		},
		//删除
		deleteClick(){
			let _self=this
			this.$dialog.confirm({
				title: '警告',
				message:
					'请注意该操作不可恢复,是否继续删除该条数据?',
			}).then(() => {
				let deleteIds=[]				//给后台删除的数组
				//先把数组里的删除
				for(let n=this.recordList.length-1;n>=0;n--){
					let item=this.recordList[n]
					if(item.checked==true){
						deleteIds.push(item.id)
						this.recordList.splice(n,1)
					}
				}
				let obj={
					ids:deleteIds
				}
				deleteGoods(obj).then(res=>{
					_self.$toast(res)
				})	
			}).catch(() => {
				// on cancel
				_self.$toast('取消')
			});
		}
	},
	computed:{
		listLen(){
			return this.$store.getters.getItem.length
		},
		recordList(){
			return this.$store.state.recordObj.list
		},
		isSelect(){
			return true
		}
		
	},
	mounted(){
			//获取vuex的值
			this.$store.dispatch('getGoods')
	},
}
</script>

<style scoped>
*{
	margin:0;
}
#record-product{
    height: 100vh;
    position: relative;
}
.record-product-nav{
	background:#516df5;
	position:relative;
	width:100%;
}

/deep/ .van-ellipsis {
}
/deep/ .van-nav-bar__text {
	color: white;
}
/deep/ .van-nav-bar .van-icon {
	color: #ffffff;
}
.popup-title{
	font-weight:500;
	font-size:15px;
	text-align:center;
	line-height:40px;
	margin-top:6px;
	color:#7e7e7e;
}
.group-box{
	margin-top:10px;
}
.btn-box{
	text-align:center;
	margin:15px 0;
}
.bottom-box{
	position:relative;
	background:#3c3c3c;
	z-index:10;
	height:46px;
	-line-height:46px;
	-text-align:center;
}
bottom-box i{
	width:50%;
	font-style:normal;
	color:white;
	background:#516df5;
	display:inline-block;
	line-height:30px;
	border-radius:6px;
}
.carImg{
	position:absolute;
	top:-35%;
	left:10px;
	height:50px;
	z-index:99;
}
.badge{
	background:#ff6161;
	position:absolute;
	top:-2px;
	right:-8px;
	border-radius:50px;
	width:22px;
	height:22px;
	text-align:center;
	line-height:22px;
	font-style:normal;
	font-size:13px;
	color:white;
}
.allSelect{
	border:1px solid white;
	border-radius:4px;
	padding:2px 8px;
	font-style: normal;
	display:block;
}
.select-box{
		position:absolute;
		right:18px;
		color:white;
		font-size:13px;
		top:25%;
}
.delete-box{
		position:absolute;
		right:80px;
		color:white;
		font-size:13px;
		top:25%;
}
</style>
