<template>
<div class="record-content">
<van-list
class="vant-list"
  finished-text="没有更多了"
  v-model="loading"
   @load="onLoad"
   :offset="10"
   :finished="finished"
>
	<record-item @deleteClick="deleteClick(item,index)" v-for="(item,index) in recordList" :key="index" :item-obj="item" @click.native="changeClick(item)"/>
</van-list>
</div>
	
</template>

<script>
import {List,DropdownMenu} from 'vant'
import RecordItem from './RecordItem'
import {deleteGood} from 'network/good'

export default {
  name: 'RecordContent',
  components:{
	[List.name]:List,
	[DropdownMenu.name]:DropdownMenu,
	RecordItem,
  },
  data(){
	return{
		loading:true,
		finished:true,
		refreshing:true,
		recordObj:{
			page:0,
			list:[],
			total:10
		},
	}
  },
  props:{

  },
  methods:{
	onLoad(){
	},
	onConfirm (){
		
	},
	itemClick(item){
		item
	},

	changeClick(item){
		//这里需要用到 vuex
		//这里也可以写进缓存里面
		let _self=this
		//执行vuex的添加或者删除方法
		this.$store.dispatch('addItem',item).then(res=>{_self.$toast(res)})
	},
	//删除一条数据
	deleteClick(item,index){
		let _self=this
		this.$dialog.confirm({
			title: '警告',
			message:
				'请注意该操作不可恢复,是否继续删除该条数据?',
		}).then(() => {
			//console.log(item,index)
			deleteGood(item.id).then(res=>{
				_self.$toast(res)
				//延迟删除数组的信息
				_self.recordList.splice(index,1)
			})
		}).catch(() => {
			// on cancel
			//_self.$toast('取消')
		});
	}
  },
  computed:{
	recordList(){
		return this.$store.state.recordObj.list
	}
  },


}
</script>

<style scoped>
.record-content{
	height:calc(100% - 92px);
	overflow:auto;
	position:relative;
	background:#fff;
	z-index:9;
}
</style>
